
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../images/background.png) !important;
    color: rgb(204, 0, 0);
    overflow: hidden;
}

.loader-container {
    display: grid;
    align-items: center;
    justify-items: center;
    background:rgba(0, 0, 0, 0.8);
    height: 100vh;
    
}

.loader-gif {
    height: 140px;
    padding-bottom: 10px;
}

header {
    height: 100px;
    background: rgb(34, 34, 34);
    display: flex;
    align-items: center;
    
}

.logo-div {

    position: relative;
    left: 50px;
}
.score-div {
    display: flex;
    right: 50px;
    position: absolute;
    top: 35px;
}

.display-cards {
    display: grid;
    padding: 50px;
    margin: 50px;
    row-gap: 20px;
    column-gap: 20px;
    grid-template-columns: repeat(8, 1fr);
}

.cards {
    display: grid;
    align-items: center;
    justify-items: center;
    height: auto;
    cursor: pointer;
    padding: 50px 0 50px 0;
    background: rgb(34, 34, 34);
    box-shadow: 3px 3px 10px white;
    /* transition: all 200ms ease 0s; */
}

.cards:hover {
    /* transition: 1s; */
    box-shadow: 3px 3px 10px rgb(204, 0, 0);
    transform: scale(1.1);
    transform: translate(10px);
}

.cards img {
    padding-bottom: 10px;
}

.restart {
    display: grid;
    height: 100vh;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(80, 8, 8, 0.9);
    color: white;
}

.restart-btn {
    border: solid white;
    background: transparent;
    height: 50px;
    width: 150px;
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.restart-btn:hover {
    background: rgba(0, 0, 0, 0.8);

}